@import "modal.scss";
@import "defaults.scss";
// @import "fonts.scss";

img {
  width: 100%;
  // height: 100%;
  border-radius: 24px;
}


svg {
  width: 1.2rem;
  height: 1.2rem;
}

.formSearch {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.star, 
.room-desc,
.room-desc > div,
.fieldset,
.logo,
.form--header {
  display: flex;
  flex-direction: row;
}

.room-desc > div,
.room-desc,
.form--header {
  align-items: center;
  justify-content: space-between;
}

.fieldset,
.logo {
  align-items: center;
}

.fieldset {
  border: none;
  box-shadow: 1px 1px 5px #f2f2f2;
  border-radius: 16px;
}

.add {
  border-top: none;
  border-bottom: none;
  background-color: #ffffff;
  margin: 1rem;
  
}

label.fa {
  display: block;
  color: red;
  padding-inline-start: 1rem;
  padding-inline-end: 1rem;
  width: 100%;
  height: 100%;
  padding: 2rem;
  cursor: pointer;
}

label.fa ~ input {
  visibility: hidden;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items : center;
}

@media (min-width : 620px) {
  .card--container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 5%;
  }
}
